.config-container{
    margin: auto;
    padding: 2%;
    width: 20%;
    height: 60vh;
    border: solid black;
    overflow: hidden;
    h1{
        padding: 1%;
        font-size: 2vw;
        text-align: center;
    }
    div{
        width: 100%;
        height: fit-content;
        font-size: 1.8vw;
        margin:10% 0 10% 0;
        input,select{
            width: 100%;
            font-size: 1.5vw;
            text-align: center;
            
        }
    }
    button{
        display: block;
        padding: 1% 5% 1% 5%;
        font-size: 1.5vw;
        margin-top: 20%;
        margin-left: auto;
        margin-right: auto;
    }
}

.question-card-container{
    width:30%;
    height: fit-content;
    border: solid black;
    margin: auto;
    padding-bottom: 2.5%;
    border-radius: 5%;
    p{
        text-align: center;
        font-size: 1.8vw;
        padding: 2%;
    }
    label{
        display: block;
        max-width: 100%;
        text-align: center;
        padding: 5% 5% 5% 5%;
        font-size: 1.5vw;
        input{
            opacity: 0;
            float: left;
        }
    }
    button{
        display: block;
        width: 50%;
        margin: auto;
        font-size: 1.5vw;
        padding: 2% ;
        background-color: blue;
        color: white;
        border: none;
        
    }
}

.option-group{
    padding-bottom: 20%;
    overflow: hidden;
}

.score-card-container{
    margin: auto;
    border: solid black;
    text-align: center;
    width:  40%;
    height: 40vh;
    display: flex;
    span{
        margin: auto;
        h1{font-size: 3vw;}
        h2{font-size: 2.5vw;}
    }
}